<template>
  <div >
    <ul >
        <li ><input type="text" v-model="Qna.name" placeholder="이름" style="width:250px;  border-radius: 5px;" ></li>
        <li ><input type="pass" v-model="Qna.pass" placeholder="pass" style="width:250px;  border-radius: 5px;"></li>
        <li ><input type="text" v-model="Qna.title" placeholder="제목" style="width:250px;  border-radius: 5px;" ></li>
        <li ><textarea type="text" v-model="Qna.content" placeholder="내용" style="width:250px; height:60px; border-radius: 5px; " /></li>
        <li ><button @click="addQna"> 등록</button></li>
    </ul>
    </div>    
</template>

<script>
import Constant from '../Constant.js'
export default {
    name:'InputQna',
    data: function(){
        return{
            Qna:{ 
            content:"",
            title:"",
            pass:"",
            name:""}
           
        }
    },
    methods:{
        addQna(){
            if( this.Qna.content!="" &&
                this.Qna.name!="" &&
                this.Qna.title!="" &&
                this.Qna.pass!="" ){
                this.$store.dispatch(Constant.ADD_QNA,{Qna:this.Qna})
           this.clear();
           
           alert("등록완료");
           this.$router.push("/list");
            }
            else{alert("입력 내용을 확인하세요")}
        },
        clear(){
          
        }
    }
}
</script>

