<template>
  <div>
    <ul>
        <li v-for="(qna) in qnalist" v-bind:key="qna.num" @click.stop="detailQna(qna.num)">
        <!-- <i class="checkBtn fas fa-check" @click.stop="completeTodo(qna.num)">●</i> -->
        <span >
          --{{qna.wdate}}
           </span>  
           <span class="list-item">
           </span>  
        <span class="list-item" style="align:center">
           {{qna.title}}
           </span>  
         <button class="btn btn-danger" type="button" @click.stop="removeQna(qna.num)">
            <i>삭제</i>
        </button>
      </li>
    </ul>
  </div>
</template>

<script>
import Constant from '../Constant';

export default {
  computed: {
    //store안의 todolist check
    qnalist() {
      return this.$store.state.qnalist; 
    }
  },
  created () {
     this.$store.dispatch(Constant.GET_QNALIST);
  },
  methods: {
    
    removeQna : function(payload){
      this.$store.dispatch(Constant.REMOVE_QNA, payload);
    }, 
    
      detailQna : function(num){
       this.$router.push("detail/"+num);
     },
    
  },
};
</script>

<style scoped>
.btn {
  display: inline-block;
  font-weight: 400;
  color: #212529;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 0.125rem solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.5rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.btn-primary {
  color: #fff;
  background-color: #1abc9c;
  border-color: #1abc9c;
}
ul {
  list-style-type: none;
  padding-left: 0px;
  margin-top: 0;
  text-align: center;
}
li {
  display: flexbox;
  min-height: 50px;
  height: 50px;
  line-height: 50px;
  margin: 0.5rem 0;
  padding: 0 0.9rem;
  background:#2c3e50;
  border-radius: 5px;
}
.checkBtn {
  line-height: 45px;
  color: #62acde;
  margin-right: 5px;
}
.removeBtn {
  margin-left: auto;
  color:#1abc9c;
}

.list-item {
  display: inline-block;
  margin-right: 10px;
}
.list-move {
  transition: transform 1s;
}
.list-enter-active,
.list-leave-active {
  transition: all 1s;
}
.list-enter,
.list-leave-to {
  opacity: 0;
  transform: translateY(30px);
}
.done{
  background-color: lightslategray;
}
.btn-danger {
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545;
}
</style>