<template>
  <div>
    <table>
      <tbody>
        <tr>
          <td>제목</td>
          <td>            
            {{qna.title}}
          </td>
        </tr>
        <tr>
          <td>작성자</td>
          <td>            
            {{qna.name}}
          </td>
        </tr>
        <tr>
          <td>등록일</td>
          <td>
           {{qna.wdate}}
          </td>
        </tr>
        <tr>
          <td>질문내용</td>
          <td>
           {{qna.content}}
          </td>
        </tr>
        <tr>
          <td>답변 <button @click="addAns(qna)">답변 등록</button></td>
          <td>
            <textarea name="" id="" cols="30" rows="10" v-model="qna.ans" @click="cleanAns(qna.ans)"></textarea>
          </td>
        </tr>
       </tbody>
    </table>
  </div>
</template>

<script>
import Constant from "../Constant";
export default {
  
  created() {
    console.log(this.$route.params.num);
    
    this.detailQna(this.$route.params.num)
    //this.clear()
  },
  computed: {
    qna(){
        return this.$store.state.qna;
    }
  },

  methods: {
    detailQna(num) {
      this.$store.dispatch(Constant.DETAIL_QNA, {num:num});
    },
    cleanAns(ans){
      if(ans=="아직 답변이 없습니다"){
        this.qna.ans="";
      }
    },
    addAns(qna){
      if(qna.ans!=""){
        this.$store.dispatch(Constant.ADD_ANS, {qna:this.qna});
        alert("답변 등록 완료")
      }else{alert("답변을 입력하세요")}
    }
  }
};
</script>

