import Vue from 'vue';
import Vuex from 'vuex';
import Constant from './Constant.js';
import axios from './axios-common';

Vue.use(Vuex);
const store = new Vuex.Store({
    //state ...
    state:{
        qnalist:[],
        qna:{}
    },
    actions: { // 외부와의 통신을 통해 데이터 처리 dispath에 의해 호출됨.        
        
        [Constant.GET_QNALIST]: store => {
            axios
            .get("/safefood/qna")
            .then((response)=>{
                store.commit(Constant.GET_QNALIST,{qnalist:response.data});
            })
            .catch()
        },
        [Constant.ADD_QNA]: (store, payload) => {  
            //console.log(payload.Qna.content);
                       
            axios
            .post("/safefood/qna", payload.Qna)
            .then(()=>{
                //console.log(payload.Qna);
                store.dispatch(Constant.GET_QNALIST)
            })
            .catch()
        },
        [Constant.REMOVE_QNA]: (store, payload) => {
           axios
           .delete("/safefood/qna/"+payload)
           .then(()=>{
               store.dispatch(Constant.GET_QNALIST);
           })

         },
        [Constant.ADD_ANS]: (store, payload) => {
            console.log(payload.qna.ans);
            console.log(payload.qna.num);

            axios
            .put("/safefood/qna/"+payload.qna.ans+"/"+payload.qna.num)
            .then(()=>{
                store.dispatch(Constant.DETAIL_QNA,{num:payload.qna.num});
            })
        },

        [Constant.DETAIL_QNA]: (store, payload) => {
            axios
            .get("/safefood/qna/"+payload.num)
            .then((response)=>{
                store.commit(Constant.DETAIL_QNA, {qna:response.data});
            })

        },
       /*
        [Constant.COMPLETE_TODO]: (store, payload) => {
            
        },
        */
       
    },
    mutations: { // 저장소에 데이터 실제 반영(commit시 호출됨)
       
        [Constant.GET_QNALIST]: (state, payload) => {
            state.qnalist=payload.qnalist
        },
        // [Constant.ADD_QNA]: (state, payload) => {            
        //     state.todolist.todo=payload.todo
        //     state.todolist.endDate=payload.endDate
        // },
        
         [Constant.DETAIL_QNA]: (state, payload) => {            
             state.qna=payload.qna
            // state.todolist.endDate=payload.endDate
         },
        /*
        [Constant.ADD_TODO]: (state, payload) => {
            state.message = payload.message;

        }
        */
    }
});


export default store;